.manageform_container {
    padding: 2rem;
}

.manageform_field {
    background-color: #1d6976;
    padding: 2rem;
    margin: 1rem;
    border-radius: 7px;
}


    
.manageform_text {
    width: 20rem;
    font-size: x-large;
    color: #ffffff;
}

.manageform_delete {
    margin-left: 1rem;
	padding: 5px 16px;
	width: 7rem;
	border-radius: 5px;
	display: inline-block;
	outline: transparent;
	border-style: solid;
	border-color: #ffffff;
	text-align: center;
    float: right;
}